module.exports = "@charset \"UTF-8\";\n@font-face {\n  font-family: Segoe-UI;\n  src: url(\"/assets/fonts/segoe-ui-light-2.ttf\");\n  font-weight: 400;\n  font-style: normal;\n}\n* {\n  font-family: Segoe-UI;\n}\nion-header {\n  background: #438DED !important;\n  --background:$cBlue!important;\n}\nion-header ion-toolbar {\n  background: #438DED !important;\n  --background:$cBlue!important;\n}\nion-header ion-buttons {\n  color: #fff;\n}\nion-header ion-title {\n  color: #fff;\n  font-size: 18px;\n  padding: 0 !important;\n}\n.clgreen {\n  text-transform: capitalize;\n  border-radius: 50px !important;\n  --border-radius: 50px!important;\n  box-shadow: 0px 2px 1px 0px rgba(3, 221, 43, 0.15);\n  --box-shadow: 0px 2px 1px 0px rgba(3,221,43,0.15);\n  background: #2ad0a8;\n  --background: $cGreen;\n}\n.clgreen:hover {\n  background: #2ad0a8;\n  --background: $cGreen;\n}\n.radiusthleft {\n  border-top-left-radius: 10px;\n}\n.radiusthright {\n  border-top-right-radius: 10px;\n}\ntable {\n  width: 100%;\n  border-collapse: collapse;\n}\n/* Zebra striping */\ntr:nth-of-type(odd) {\n  background: #fff;\n}\nth {\n  background: #438DED;\n  color: white;\n  font-weight: 400;\n}\ntd, th {\n  padding: 8px;\n  border: 0;\n  text-align: left;\n  font-family: Segoe-UI;\n  font-size: 14px;\n}\n/*\nMax width before this PARTICULAR table gets nasty\nThis query will take effect for any screen smaller than 760px\nand also iPads specifically.\n*/\n@media only screen and (max-width: 760px), (min-device-width: 768px) and (max-device-width: 1024px) {\n  /* Force table to not be like tables anymore */\n  table, thead, tbody, th, td, tr {\n    display: block;\n  }\n\n  /* Hide table headers (but not display: none;, for accessibility) */\n  thead tr {\n    position: absolute;\n    top: -9999px;\n    left: -9999px;\n  }\n\n  tr {\n    border: 1px solid #ccc;\n  }\n\n  td {\n    /* Behave  like a \"row\" */\n    border: none;\n    border-bottom: 1px solid #eee;\n    position: relative;\n  }\n\n  td:before {\n    /* Now like a table header */\n    position: relative;\n    /* Top/left values mimic padding */\n    top: 0px;\n    left: 6px;\n    width: 45%;\n    padding-right: 10px;\n    white-space: nowrap;\n  }\n\n  /*\n  Label the data\n  */\n  td:nth-of-type(1) {\n    background: #2ad0a8;\n    color: #fff !important;\n    --color: #fff!important;\n  }\n\n  td:nth-of-type(1):before {\n    content: \"Código:\";\n    color: #fff;\n    --color: #ffffff;\n  }\n\n  td:nth-of-type(2):before {\n    content: \"Nombre:\";\n    color: #2ad0a8;\n    --color: #2ad0a8;\n  }\n\n  td:nth-of-type(3):before {\n    content: \"Tipo:\";\n    color: #2ad0a8;\n    --color: #2ad0a8;\n  }\n\n  td:nth-of-type(4):before {\n    content: \"Aforo:\";\n    color: #2ad0a8;\n    --color: #2ad0a8;\n  }\n\n  td:nth-of-type(5):before {\n    content: \"Aforo Superado:\";\n    color: #2ad0a8;\n    --color: #2ad0a8;\n  }\n\n  td:nth-of-type(6):before {\n    content: \"Visualizar:\";\n    color: #2ad0a8;\n    --color: #2ad0a8;\n  }\n\n  td:nth-of-type(7):before {\n    content: \"Editar:\";\n    color: #2ad0a8;\n    --color: #2ad0a8;\n  }\n\n  td:nth-of-type(8):before {\n    content: \"Eliminar:\";\n    color: #2ad0a8;\n    --color: #2ad0a8;\n  }\n}\n/* Smartphones (portrait and landscape) ----------- */\n@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {\n  body {\n    padding: 0;\n    margin: 0;\n    width: 320px;\n  }\n}\n/* iPads (portrait and landscape) ----------- */\n@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {\n  body {\n    width: 495px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"